import { DAA_TODO, DELETE_TODO, UPDATE_TODO } from "./actions";
import { todos } from "./states";

export let reducer = (state = todos, action) => {
    let newTodos;
  switch (action.type) {
    case DAA_TODO:
      newTodos = [...state];
      newTodos.unshift(action.payload);
      return newTodos;



    case DELETE_TODO:
       newTodos = [...state];
      newTodos = newTodos.filter((todo) => todo.id !== action.payload);
      return newTodos;


      
    case UPDATE_TODO:
      newTodos=[...state];
      let index = -1
      for(let i=0; i< newTodos.length; i++){

        index++;
        if(newTodos[i].id === action.payload.id){
            break;
        }
        console.log(index);
      }
      if(index !== -1){
          newTodos[index]=action.payload;
          return newTodos;
      }

  
  }
  return state;
} ;
